import { Component } from "react";
import Header from './Header';
import axios from 'axios';
import lang from "../language/lang";
const baseurl = process.env.REACT_APP_API_BASE_URL;
const academyList = {
    jp:[
        "アカデミ",
        "アカデミ",
        "アカデミ"
    ],
    en:[
        "Risedot",
        "Risedot",
        "Risedot"
    ]
}
class ProfileEdit extends Component{
    constructor(props)
    {
        super(props)
        this.state={
            currentPassword: "",
            newPassword: "",
            errorPassword:"",
            changed:false,
            language:JSON.parse(localStorage.language).language
        }
    }


    handleChange = filedName => e=>{
        this.setState({
            [filedName]:e.target.value
        })
    }

    handleSubmit = e =>{
        var userData =  JSON.parse(localStorage.getItem("userData")) || null;
        var token = userData.token
        const{currentPassword, newPassword, language} = this.state
        var data = JSON.stringify({"oldPassword":currentPassword,"newPassword":newPassword});
        var config = {
            method: 'post',
            url: `${baseurl}/api/changePassword`,
            headers: { 
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token,
            },
            data : data
          };
          axios(config)
          .then(async (response)=>{
            this.setState(
                {
                    changed:true
                }
            )
           })
          .catch((error)=>{
              this.setState(
                  {errorPassword:lang[language].notMatchPassword}
              )
           });
    }

    handleLogout = (event)=>{
        localStorage.removeItem("adminData");
        window.location.assign('/');
    }

    langClickSp = event=> {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        else {
            langSelect[0].style.maxHeight = langSelect[0].scrollHeight + "px";
        }
    }

    langSelectSp = x => event => {
        var langSelect = document.getElementsByClassName('header-lang-select-sp');
        if (langSelect[0].style.maxHeight) {
            langSelect[0].style.maxHeight = null;
        }
        localStorage.setItem("language", JSON.stringify({language:x}))
        this.setState({language:x});
        window.location.reload(); 
    }

    render()
    {
        const{currentPassword, newPassword, errorPassword, language} = this.state
        return(
            <>
                <main>
                        <header>
                            <h2>{lang[language].changeinfor}</h2>
                        </header>
                        <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                            <Header/>
                            <div class="container1">
                                <div class="crumb">
                                    <p><a href="/dashboard/home">{lang[language].top}</a> &nbsp;&gt;&nbsp; <a href="/dashboard/profile">{lang[language].profile}</a>&nbsp;&gt;&nbsp;{lang[language].changePass}</p>
                                    <div className="header-lang-sp" onClick={this.langClickSp}>
                                        <div  className={language==="jp"? "header-lang-btn-sp header-lang-jp":"header-lang-btn-sp header-lang-us"}></div>
                                        <div className="header-lang-arrow-sp"></div>
                                        <div className="header-lang-select-sp" onClick={(e)=>{e.stopPropagation();}}>
                                            <div>
                                                <div onClick={this.langSelectSp('jp')}> <img src="/assets/img/icon_lang_jpn.png" alt=""/></div>
                                                <div onClick={this.langSelectSp('en')}><img src="/assets/img/icon_lang_usa.png" alt=""/></div>
                                            </div>
                                        </div>
                                    </div>
                                    <a onClick={this.handleLogout} className="logout-sp">{lang[language].log_out}</a>
                                </div>
                                {this.state.changed ? 
                                <>
                                    <div class="information-container change-pass-container1">
                                        <p>{lang[language].passwordChanged}</p>
                                    </div>
                                </>
                                :
                                <>
                                    <div class="information-container">
                                        <div class="input-box">
                                            <label for="">{lang[language].currentPass}</label>
                                            <input type="password" value={currentPassword} onChange={this.handleChange("currentPassword")} />
                                        </div>
                                        <span className="error">{errorPassword}</span>
                                        <div class="input-box">
                                            <label for="">{lang[language].newPass}</label>
                                            <input type="password" value={newPassword} onChange={this.handleChange("newPassword")}/>
                                        </div>
                                    </div>
                                    <div class="information-container change-pass-container">
                                        <a onClick={this.handleSubmit} class="white-btn information-btn">
                                            <span>{lang[language].change}</span>
                                        </a>
                                        <a href="/dashboard/profile" class="white-btn information-btn">
                                            <span>{lang[language].cancel}</span>
                                        </a>
                                    </div>
                                </>
                                }
                            </div>
                        </section>
                    </main>
            </>
        )
    }
}

export default ProfileEdit;